
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import { getJson } from '../survey'
export default {
name: 'Question',
components: {
Checker,
CheckerItem
},
data () {
return {
num: '1',
dataList: {},
title: '',
question: '',
option: [],
checked: [],
typeStyle: 'radio',
allQuestion: 10,
singleQuestion: 6
}
},
methods: {
changeAnswer () {
let currentSelect = []
console.log('this.checked', this.checked[0])
if (this.num <= this.singleQuestion) {
switch (this.checked) {
case 1:
currentSelect = ['A']
break
case 2:
currentSelect = ['B']
break
case 3:
currentSelect = ['C']
break
case 4:
currentSelect = ['D']
break
}
} else {
for (let i = 0; i < this.checked.length; i++) {
switch (this.checked[i]) {
case 1:
currentSelect[i] = ['A']
break
case 2:
currentSelect[i] = ['B']
break
case 3:
currentSelect[i] = ['C']
break
case 4:
currentSelect[i] = ['D']
break
}
}
}
console.log('currentSelect', currentSelect)
console.log('this.checked', this.checked)
let questionRes = cookie.get('QuestionResult')
let arr = JSON.parse(questionRes)
arr[this.num - 1] = currentSelect
console.log('arr', arr)
arr = JSON.stringify(arr)
cookie.set('QuestionResult', arr, {
raw: true
})
},
setCurrentQuestion () {
let paramsId = this.$route.params.id
let questionNum = paramsId * 1 - 1
let currentQuestion = this.dataList[questionNum]
if (typeof currentQuestion !== 'undefined') {
if (questionNum < this.singleQuestion) {
this.checked = []
this.typeStyle = 'radio'
this.title = '（第' + paramsId + '题）-单选题'
this.question = currentQuestion.question
this.option = currentQuestion.option
} else {
this.typeStyle = 'checkbox'
this.title = '（第' + paramsId + '题）-多选题'
this.checked = []
this.question = currentQuestion.question
this.option = currentQuestion.option
}
}
},
prevQuestion () {
this.num--
this.$route.params.id = this.num
this.$router.back()
},
nextQuestion () {
if (this.checked.length === 0) { // 未选择不可进行下一题
alert('请选择')
} else {
if (this.num < this.allQuestion) { // 总题目数量
this.num++
this.checked = []  
this.$router.push({
name: 'Question',
params: {
id: this.num.toString()
}
})
} else {
this.$router.push({
name: 'Result'
})
}
}
},
setCookie () {
if (typeof cookie.get('QuestionResult') === 'undefined') {
let arr = JSON.stringify([])
cookie.set('QuestionResult', arr, {
raw: true
})
}
}
},
watch: {
num: function (val, oldVal) {
if (val !== oldVal) {
this.setCurrentQuestion()
}
}
},
mounted () {
let that = this
this.setCookie()
that.num = this.$route.params.id
console.log('11111111111111', this.num)
getJson().then((res) => {
that.dataList = res.Question
that.setCurrentQuestion()
})
}
}



