
import cookie from 'vux/src/tools/cookie/index.js'

import { getJson } from '../survey'

export default {
name: 'Result',
data () {
return {
fraction: 0,
answer: [],
youAnswerArr: []
}
},
methods: {
reStart () {




this.$router.push({
name: 'Question',
params: {
id: '1'
}
})
},
getFraction () {
if (typeof cookie.get('QuestionResult') !== 'undefined') {
this.youAnswerArr = JSON.parse(cookie.get('QuestionResult'))

}
},
calculateFraction () {
for (let i = 0; i < this.answer.length; i++) {
let youAnswer = this.youAnswerArr[i].sort().join('')
if (this.answer[i] === youAnswer) {
this.fraction = this.fraction + 10
}
}
}
},
mounted () {
let that = this
this.getFraction()
getJson().then((res) => {
that.answer = res.Answer
console.log('answer', that.answer)
that.calculateFraction()
})
}

}



